import React, { Component } from 'react';
import {  Input, Button, Form} from 'antd';

class AddAndEditModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            menuType:"1"
        }
    }
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps) !== JSON.stringify(this.props)) {
            // 重置所有表单项 
            this.props.form.resetFields()
        }
    }
    changeRadio = (e) => {
        // console.log(e.target.value);
        this.setState({menuType:e.target.value})
    }
    //提交
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
          if (!err) {
            this.props.handleOK(values);
          }
        });
    }
    //取消
    cancle = () => {
        this.props.handleCancle();
    }
    render() {
        
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
          };
          const tailFormItemLayout = {
            wrapperCol: { span: 24 }
          };
        const { getFieldDecorator } = this.props.form;
        let {recordInfo,modalType}=this.props
        if(recordInfo.permissionId && modalType === 'add'){
            recordInfo={}
        }
        return (
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                {/* <Form.Item label="菜单类型">
                    {getFieldDecorator('menuType', {
                        initialValue: recordInfo.menuType
                    })(
                        <Radio.Group onChange={this.changeRadio}>
                            <Radio value="1">一级菜单</Radio>
                            <Radio value="2">子菜单</Radio>
                        </Radio.Group>
                    )}
                </Form.Item> */}
                <Form.Item label="菜单名称">
                    {getFieldDecorator('name', {
                        rules: [
                            {
                                required: true,
                                message: '请输入菜单名称',
                            }
                        ],
                        initialValue: recordInfo.name
                    })(<Input placeholder="请输入菜单名称"/>)}
                </Form.Item>
                {/* {
                    this.state.menuType!=="1" && 
                    <Form.Item label="上级菜单">
                        {getFieldDecorator('superMenu', {
                            rules: [
                                {
                                    required: true,
                                    message: '请选择上级菜单',
                                }
                            ],
                            initialValue: recordInfo.superMenu
                        })(
                            <Select>
                                <Select.Option value="1001">填报计划</Select.Option>
                                <Select.Option value="9001">系统设置</Select.Option>
                            </Select>
                        )}
                    </Form.Item>
                } */}
                <Form.Item label="菜单路径">
                    {getFieldDecorator('url', {
                        rules: [
                            {
                                required: true,
                                message: '请输入菜单路径',
                            }
                        ],
                        initialValue: recordInfo.url
                    })(<Input placeholder="请输入菜单路径"/>)}
                </Form.Item>
                <Form.Item label="图标路径">
                    {getFieldDecorator('icon', {
                        
                        initialValue: recordInfo.icon
                    })(<Input placeholder="请输入图标路径"/>)}
                </Form.Item>
                <Form.Item label="排序">
                    {getFieldDecorator('sortNo', {
                        initialValue: recordInfo.sortNo
                    })(<Input/>)}
                </Form.Item>
                <Form.Item {...tailFormItemLayout} style={{textAlign:"center",marginBottom:"0"}}>
                    <Button type="primary" htmlType="submit" style={{margin:"0px 16px"}}> 确认 </Button>
                    <Button onClick={this.cancle}> 取消 </Button>
                </Form.Item>
            </Form>
        )
    }
}

export default Form.create()(AddAndEditModal);