import React, { Component } from 'react';
import { Modal, Table, Button, Divider, message, Popconfirm } from 'antd';
import AddAndEditModal from './addAndEditModal';
import AddAddEditButtonModal from './addAddEditButtonModal';
import DetailModal from './detailModal'
import styles from './index.module.less';
import {postAction} from '@/axios'
import SystemTabs from '../common/systemTabs';

export default class menuManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            showAddMenuModal:false,//修改/添加菜单弹窗
            showAddButtonModal:false,//修改/添加按钮弹窗
            modalType:"add",
            showDetailModal:false,
            recordInfo:{},
            selectedRows:[],//选择行数据
            columns:[               
                {
                    title:"菜单名称",
                    dataIndex:"name",
                    with:'20%'
                },{
                    title:"类型",
                    dataIndex:"type",
                    width:'15%',
                    render:(text,record)=>(text===1?record.parentId?"子菜单":"一级菜单":"按钮")
                },{
                    title:"路径",
                    dataIndex:"url",
                    width:'35%'
                },{
                    title:"排序",
                    dataIndex:"sortNo",
                    width:'5%'
                },{
                    title:"操作",
                    align:'center',
                    width:'25%',
                    render:(text,record)=>{
                        return record.disabled?"":<React.Fragment>
                            <span className="operation-link" onClick={()=>{this.handleOpeneditModal(record)}}>编辑</span>
                            <Divider type="vertical"/>
                            {
                                record.type === 1?<React.Fragment>
                                    {
                                        record.parentId?<React.Fragment>
                                                <span className="operation-link" onClick={()=>{this.handleAddBButton(record)}}>添加按钮</span>
                                                <Divider type="vertical"/>
                                            </React.Fragment>:
                                            (<React.Fragment>
                                                {
                                                    record.children && record.children.length>0?<React.Fragment>
                                                        {
                                                            record.children[0].type===1?<React.Fragment>
                                                                 <span className="operation-link" onClick={()=>{this.handleAddMenu(record)}}>添加子菜单</span>
                                                                <Divider type="vertical"/>
                                                            </React.Fragment>:<React.Fragment>
                                                                <span className="operation-link" onClick={()=>{this.handleAddBButton(record)}}>添加按钮</span>
                                                                <Divider type="vertical"/>
                                                            </React.Fragment>
                                                        }
                                                    </React.Fragment>:<React.Fragment>
                                                        <span className="operation-link" onClick={()=>{this.handleAddBButton(record)}}>添加按钮</span>
                                                        <Divider type="vertical"/>
                                                        <span className="operation-link" onClick={()=>{this.handleAddMenu(record)}}>添加子菜单</span>
                                                        <Divider type="vertical"/>
                                                    </React.Fragment>
                                                }
                                            </React.Fragment>)
                                    }
                                </React.Fragment>:null
                            }
                            
                            <Popconfirm
                                title="确定删除该菜单吗？"
                                onConfirm={(e)=>{this.deleteMenu(e,record)}}
                                // onCancel={cancel}
                                okText="确定"
                                cancelText="取消"
                            >
                                <span className="operation-link">删除</span>
                            </Popconfirm>
                        </React.Fragment>
                    }
                }
            ],
            dataSource:[],
            selectedRowKeys:[],
        }
    }
    //打开添加按钮弹窗
    handleAddBButton = (record = {}) => {
        this.setState({modalType:"add",showAddButtonModal:true,recordInfo:record})
    }
    //打开新建子菜单弹窗
    handleAddMenu = (record = {}) => {
        this.setState({modalType:"add",showAddMenuModal:true,recordInfo:record})
    }
    // 新建一级菜单
    handleAddMenuOne=()=>{
        this.setState({modalType:"add",showAddMenuModal:true})
    }
    //打开编辑弹窗
    handleOpeneditModal = (record) => {
        if(record.type===1){//菜单
            this.setState({modalType:"edit",showAddMenuModal:true,recordInfo:record})
        }else{//按钮
            this.setState({modalType:"edit",showAddButtonModal:true,recordInfo:record})
        }
    }
    //发送新增/编辑菜单/按钮请求
    handleUpdate = (info) => {
        let param=info
        if(this.state.modalType==="add"){
            param.parentId=this.state.recordInfo?this.state.recordInfo.permissionId:null
            if(this.state.showAddButtonModal){// 增加按钮
                param.type=2
            }else{// 增加菜单
                param.type=1
            }
            postAction(
                `/ybt/system/setting/permission/createPermission`,
                param
            ).then(res=>{
                if(res.success){
                    message.success('操作成功')
                    this.getData()
                }else{
                    message.error(res.obj)
                }
            })
        }else{
            param.permissionId=this.state.recordInfo.permissionId
            postAction(
                `/ybt/system/setting/permission/updatePermission`,
                param
            ).then(res=>{
                if(res.success){
                    message.success('操作成功')
                    this.getData()
                }else{
                    message.error(res.obj)
                }
            })
        }
        this.setState({showAddMenuModal:false,showAddButtonModal:false,recordInfo:{}})
    }
    //关闭新增/编辑菜单弹窗
    handleCancleEdit = () => {
        this.setState({showAddMenuModal:false,recordInfo:{}})
    }
    //关闭新增/编辑按钮弹窗
    handleCancleButton = () => {
        this.setState({showAddButtonModal:false,recordInfo:{}})
    }
    //打开查看详情弹窗
    showDetail = (record={}) => {
        this.setState({showDetailModal:true,recordInfo:record})
    }
    //删除
    deleteMenu = (e,record) => {
        if(record){
            postAction(
                `/ybt/system/setting/permission/deletePermission`,
                [record.permissionId]
            ).then(res=>{
                if(res.success){
                    message.success('操作成功')
                    this.getData()
                }else{
                    message.error(res.obj)
                }
            })
        }else{
            if(this.state.selectedRowKeys.length>0){
                Modal.confirm({
                    title:"确定删除这些选中项吗？",
                    onOk:() => {
                        postAction(
                            `/ybt/system/setting/permission/deletePermission`,
                            this.state.selectedRowKeys
                        ).then(res=>{
                            if(res.success){
                                message.success('操作成功')
                                this.getData()
                            }else{
                                message.error(res.obj)
                            }
                        })
                    },
                    onCancel:() => {
    
                    },
                    okText:"确定",
                    cancelText:"取消"
                })
            }else{
                message.warning('请至少选择一项')
            }
            
            
        }
    }
    componentDidMount() {
        this.getData()
    }
    getData=()=>{
        postAction(`/ybt/system/setting/permission/getPermissionList?pageNum=1&pageSize=0`).then(res=>{
            if(res.success){
                let dataSource=res.obj.rows
                this.filterChildren(dataSource)
                this.setState({
                    dataSource
                })
            }
        })
        
    }
    filterChildren=(data)=>{
        data.forEach(item=>{            
            if(item.children && item.children.length>0){
                this.filterChildren(item.children)
            }else{
                delete item.children
            }
        })
    }
    render() {
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                //console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
                this.setState({selectedRows,selectedRowKeys})
            },
            getCheckboxProps: record => ({
              disabled: record.disabled || false
            }),
        };
        return (
            <React.Fragment>
                <SystemTabs activeTab="menuManagement"/>
                <div className={styles.container}>
                    <div className={styles.card}>
                        {/* <div className={styles.cardTitle}>菜单管理</div> */}
                        <div className={styles.operation}>
                            <Button icon="plus" type="primary" onClick={this.handleAddMenuOne}>新增菜单</Button>
                            <Button onClick={this.deleteMenu}>批量删除</Button>
                        </div>
                        <Table
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            rowSelection={rowSelection}
                            rowKey={record=>record.permissionId}
                            onChange={this.handleTableChange}
                        />
                    </div>
                    <Modal
                        visible={this.state.showAddMenuModal}
                        title={this.state.modalType==="add"?this.state.recordInfo.permissionId?"新增子菜单":"新增菜单":"编辑菜单"}
                        onCancel={this.handleCancleEdit}
                        width={560}
                        footer={false}
                        destroyOnClose={true}
                    >
                        <AddAndEditModal modalType={this.state.modalType} recordInfo={this.state.recordInfo} handleOK={(info)=>{this.handleUpdate(info)}} handleCancle={this.handleCancleEdit}/>
                    </Modal>
                    <Modal
                        visible={this.state.showAddButtonModal}
                        title={this.state.modalType==="add"?"添加按钮":"编辑按钮"}
                        onCancel={this.handleCancleButton}
                        width={560}
                        footer={false}
                        destroyOnClose={true}
                    >
                        <AddAddEditButtonModal modalType={this.state.modalType} recordInfo={this.state.recordInfo} handleOK={(info)=>{this.handleUpdate(info)}} handleCancle={this.handleCancleButton}/>
                    </Modal>
                    <Modal
                        visible={this.state.showDetailModal}
                        onCancel={()=>{this.setState({showDetailModal:false,recordInfo:{}})}}
                        title="详情"
                        width={400}
                        footer={false}
                    >
                        <DetailModal recordInfo={this.state.recordInfo}/>
                    </Modal>
                </div>
            </React.Fragment>
        )
    }
}
