import React from 'react';
import { Row, Col } from 'antd';

export default function DetailModal(props){
    // const menuInfo = props.recordInfo;
    const menuInfo = {
        type:"2",
        menuName:"菜单名称",
        superMenu:"系统设置",
        menuUrl:"/systemSetUp/userManager",
        sort:"6"
    }
    const labelSpan = 7;
    const contentSpan = 15;
    const contentOffset = 2;
    return (
        <React.Fragment>
            <Row style={{marginBottom:"15px"}}>
                <Col span={labelSpan} style={{textAlign:"right"}}>菜单类型：</Col>
                <Col span={contentSpan} offset={contentOffset}>{menuInfo.type === "1"?"一级菜单":"子菜单"}</Col>
            </Row>
            <Row style={{marginBottom:"15px"}}>
                <Col span={labelSpan} style={{textAlign:"right"}}>菜单名称：</Col>
                <Col span={contentSpan} offset={contentOffset}>{menuInfo.menuName}</Col>
            </Row>
            {
                menuInfo.type === "1"?
                null:
                <Row style={{marginBottom:"15px"}}>
                    <Col span={labelSpan} style={{textAlign:"right"}}>上级菜单：</Col>
                    <Col span={contentSpan} offset={contentOffset}>{menuInfo.superMenu}</Col>
                </Row>
            }
            <Row style={{marginBottom:"15px"}}>
                <Col span={labelSpan} style={{textAlign:"right"}}>菜单路径：</Col>
                <Col span={contentSpan} offset={contentOffset}>{menuInfo.menuUrl}</Col>
            </Row>
            <Row style={{marginBottom:"15px"}}>
                <Col span={labelSpan} style={{textAlign:"right"}}>排序：</Col>
                <Col span={contentSpan} offset={contentOffset}>{menuInfo.sort}</Col>
            </Row>
        </React.Fragment>
    )
}
